* {
  margin: 0;
}

body {
  font-family: "Roboto", sans-serif;
  background-color: #151515;
}
.container {
  padding: 1rem;
  height: 100vh;
  color: white;
  transition: 1s ease all;

  margin: 0rem auto;
}
.movie-list {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 60px;
}
.movie-list-item {
  position: relative;
}
.movie-list-item-img {
  transition: all 1s ease-in-out;
  width: 100%;
  height: 240px;
  object-fit: cover;
  border-radius: 4px;
}

.movie-list-item-title {
  background-color: rgba(51, 51, 51, 0.548);
  padding: 0 10px;
  font-size: 24px;
  font-weight: bold;
  position: absolute;
  top: 10%;
  left: 20px;
  opacity: 1;
  transition: 1s all ease-in-out;
}

.movie-list-item-desc {
  background-color: rgba(51, 51, 51, 0.548);
  padding: 10px;
  font-size: 14px;
  position: absolute;
  bottom: -140px;
  left: 0px;
  width: 250px;
  opacity: 0;
  transition: 1s all ease-in-out;
  height: 50%;
  width: 93%;
}
.movie-list-item:hover .movie-list-item-desc {
  opacity: 1;
  bottom: 0;
}

.movie-list-item-button {
  padding: 10px;
  background-color: #4dbf00;
  color: white;
  border-radius: 10px;
  outline: none;
  border: none;
  cursor: pointer;
  position: absolute;
  bottom: 20px;
  left: 50px;
  opacity: 1;
  transition: 1s all ease-in-out;
}
