* {
  margin: 0;
}

body {
  background-color: #151515;
  font-family: Roboto, sans-serif;
}

.container {
  height: 100vh;
  color: #fff;
  margin: 0 auto;
  padding: 1rem;
  transition: all 1s;
}

.movie-list {
  grid-template-columns: repeat(4, 1fr);
  gap: 60px;
  display: grid;
}

.movie-list-item {
  position: relative;
}

.movie-list-item-img {
  width: 100%;
  height: 240px;
  object-fit: cover;
  border-radius: 4px;
  transition: all 1s ease-in-out;
}

.movie-list-item-title {
  opacity: 1;
  background-color: #3333338c;
  padding: 0 10px;
  font-size: 24px;
  font-weight: bold;
  transition: all 1s ease-in-out;
  position: absolute;
  top: 10%;
  left: 20px;
}

.movie-list-item-desc {
  width: 250px;
  opacity: 0;
  height: 50%;
  width: 93%;
  background-color: #3333338c;
  padding: 10px;
  font-size: 14px;
  transition: all 1s ease-in-out;
  position: absolute;
  bottom: -140px;
  left: 0;
}

.movie-list-item:hover .movie-list-item-desc {
  opacity: 1;
  bottom: 0;
}

.movie-list-item-button {
  color: #fff;
  cursor: pointer;
  opacity: 1;
  background-color: #4dbf00;
  border: none;
  border-radius: 10px;
  outline: none;
  padding: 10px;
  transition: all 1s ease-in-out;
  position: absolute;
  bottom: 20px;
  left: 50px;
}

